@import 'variables';

/*
 Multi Select
 */
$choices-border-radius: 0px;
$choices-border-radius-item: 0px;
$choices-bg-color: $white;
$choices-bg-color-disabled: $gray-300;
$choices-bg-color-dropdown: $white;
$choices-text-color: $white;
$choices-keyline-color: $gray-300;
$choices-primary-color: theme-color('primary');
$choices-disabled-color: #eaeaea;
$choices-highlight-color: red;
$choices-button-dimension: 8px;
$choices-button-offset: 8px;
@import '~choices.js/src/styles/choices';

/*
 Range input
 */

$track-color: $gray-100 !default;
$thumb-color: theme-color('primary') !default;

$thumb-radius: 0px;
$thumb-height: 15px !default;
$thumb-width: 15px !default;
$thumb-shadow-size: 2px !default;
$thumb-shadow-blur: 0px !default;
$thumb-shadow-color: $gray-200 !default;
$thumb-border-width: 2px !default;
$thumb-border-color: #eceff1 !default;

$track-width: 100% !default;
$track-height: 15px !default;
$track-shadow-size: 0px !default;
$track-shadow-blur: 0px !default;
$track-shadow-color: $gray-200 !default;
$track-border-width: $border-width !default;
$track-border-color: $border-color !default;

$track-radius: 0px !default;
$contrast: 10% !default;

$ie-bottom-track-color: darken($track-color, $contrast) !default;

@mixin shadow($shadow-size, $shadow-blur, $shadow-color) {
  box-shadow: $shadow-size $shadow-size $shadow-blur $shadow-color, 0 0 $shadow-size lighten($shadow-color, 5%);
}

@mixin track {
  cursor: default;
  height: $track-height;
  transition: all 0.2s ease;
  width: $track-width;
}

@mixin thumb($adjustment: 0) {
  @include shadow($thumb-shadow-size, $thumb-shadow-blur, $thumb-shadow-color);
  background: $thumb-color;
  border: $thumb-border-width solid $thumb-border-color;
  border-radius: $thumb-radius;
  cursor: default;
  height: $thumb-height + $adjustment;
  width: $thumb-width + $adjustment;
}

@mixin disabled {
  cursor: not-allowed;
}
/* stylelint-disable no-descending-specificity */
[type='range'] {
-webkit-appearance: none;
background: transparent;
margin: $thumb-height / 2 0;
width: $track-width;

&::-moz-focus-outer {
  border: 0;
}

&::-webkit-slider-runnable-track {
  @include track;
  @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
  background: $track-color;
  border: $track-border-width solid $track-border-color;
  border-radius: $track-radius;
}

&::-webkit-slider-thumb {
  @include thumb;
  -webkit-appearance: none;
  margin-top: ((-$track-border-width * 2 + $track-height) / 2 - $thumb-height / 2);
}

&::-moz-range-track {
  @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
  @include track;
  background: $track-color;
  border: $track-border-width solid $track-border-color;
  border-radius: $track-radius;
  height: $track-height / 2;
}

&::-moz-range-thumb {
  @include thumb(-4);
}

&::-ms-track {
  @include track;
  background: transparent;
  border-color: transparent;
  border-width: ($thumb-height / 2) 0;
  color: transparent;
}

&::-ms-fill-lower {
  @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
  background: $ie-bottom-track-color;
  border: $track-border-width solid $track-border-color;
  border-radius: ($track-radius * 2);
}

&::-ms-fill-upper {
  @include shadow($track-shadow-size, $track-shadow-blur, $track-shadow-color);
  background: $track-color;
  border: $track-border-width solid $track-border-color;
  border-radius: ($track-radius * 2);
}

&::-ms-thumb {
  @include thumb(-4);
  margin-top: $track-height / 4;
}

&:disabled {

    @include disabled;

    &::-webkit-slider-thumb {
      @include disabled;
    }

    &::-moz-range-thumb {
      @include disabled;
    }

    &::-ms-thumb {
      @include disabled;
    }

    &::-webkit-slider-runnable-track {
      @include disabled;
    }

    &::-ms-fill-lower {
      @include disabled;
    }

    &::-ms-fill-upper {
      @include disabled;
    }
  }


  &:focus {
    outline: 0;

    &::-webkit-slider-runnable-track {
      background: lighten($track-color, $contrast);
    }

    &::-ms-fill-lower {
      background: $track-color;
    }

    &::-ms-fill-upper {
      background: lighten($track-color, $contrast);
    }
  }

}
/* stylelint-enable */