.pace {
  -webkit-pointer-events: none;
  pointer-events: none;

  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;

  .pace-progress {
    background: theme-color('primary');
    position: fixed;
    z-index: 2;
    top: $navbar-height;
    right: 100%;
    width: 100%;
    height: 2px;
  }

}

.embedded-layout {
  .pace {
    .pace-progress {
      top: 0px;
    }
  }
}

@media screen and (max-width: 425px) {
  .embedded-layout {
    .pace {
      .pace-progress {
        height: 8px;
      }
    }
  }
}