@import 'variables';

#sidebar {
  display: flex;
  flex-direction: column;
  background: $sidebar-bg;
  width: $sidebar-width;
  height: $sidebar-height;
  flex-shrink: 0;
  transition: all 0.3s;
  overflow: hidden;
  @include borders($sidebar-borders);

  .sidebar-nav {
    flex: 1;
    position: relative;
    width: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    background: inherit;
    border: none;
    margin-top: $navbar-height;
  }

  .nav {
    width: 100%;
    list-style: none;
    background: inherit;
  }

  input.form-control {
    margin-left: 10px;
    margin-bottom: 1em;
    width: auto;
  }

  #sidebar-categories {
    width: 100%;
  }

  .nav-item {
    width: 100%;
    position: relative;
  }

  .nav-link {
    display: block;
    text-decoration: none;
    padding: $sidebar-nav-link-padding-y $sidebar-nav-link-padding-x;
    color: $sidebar-nav-link-color;
    font-size: $sidebar-nav-link-size;

    i:first-of-type, svg:first-of-type {
      display: inline-block;
      width: 24px;
      margin-right: 4px;
      color: $sidebar-nav-icon-color;
      font-size: 14px;
    }

    &:hover {
      background: $sidebar-nav-link-bg-hover-color;
      color: $sidebar-nav-link-hover-color;

      i, svg {
        color: $sidebar-nav-icon-hover-color;
      }
    }

    &.active {
      color: $sidebar-nav-link-active-color;
      background: $sidebar-nav-link-active-bg-color;

      &:hover {
        background: $sidebar-nav-link-bg-hover-color;
      }

      i {
        color: $sidebar-nav-icon-active-color;
      }
    }
  }

  .nav-dropdown-toggle {
    i:last-of-type, svg:last-of-type {
      float: right;
      margin-top: 4px;
    }
  }

  .nav-title {
    font-size: $small-font-size;
    text-transform: uppercase;
    padding: $sidebar-nav-title-padding-y $sidebar-nav-title-padding-x;
    color: darken($sidebar-nav-title-color, 30%);
  }

  .nav-dropdown-items {
    padding: 0;
    margin: 0;
    overflow: hidden;
    max-height: 0;
    transition: max-height 0.3s ease-in-out;

    .nav-item {
      padding: 0;
      list-style: none;
    }
  }

  .nav-dropdown.open {
    .nav-dropdown-items {
      max-height: 1000px;
    }

    .nav-dropdown-toggle {
      i:last-of-type, svg:last-of-type {
        transform: rotate(270deg);
      }
    }
  }

}

.sidebar-fixed {
  #sidebar {
    position: fixed;
  }
}

.sidebar-hidden {
  #sidebar {
    margin-left: -$sidebar-width;
  }
}

@include media-breakpoint-down('md') {
  .sidebar {
    margin-left: -$sidebar-width;
  }

  .sidebar-mobile-show {
    .sidebar {
      margin-left: 0;
    }
  }
  #sidebar-categories {
    input.form-control {
      display: none;
    }
  }
}

@include media-breakpoint-down('md') {
  #sidebar {
    margin-left: -$sidebar-width;
  }

  .sidebar-mobile-show {
    #sidebar {
      margin-left: 0;
    }
  }
}
