@import 'variables';

.navbar-cmdb {
  height: $navbar-height;
  background: $navbar-bg-color;
  // border-bottom: $navbar-border-width $navbar-border-style theme-color('primary');

  .navbar-brand {
    width: $navbar-brand-width;
    font-weight: $navbar-brand-font-width;

    b {
      font-weight: $navbar-brand-font-width-bold;
      padding-right: $navbar-brand-small-font-right;
    }

    small {
      font-weight: $navbar-brand-small-font-width;
      color: theme-color('primary');
      position: relative;
      top: $navbar-brand-small-font-top;
      margin-right: $navbar-brand-small-font-right;
    }
  }

  .navbar-nav {
    li {
      a {
        color: $navbar-icon-link;
      }
    }

    .dropdown-menu {
      padding: $navbar-dropdown-menu-padding;
      margin-top: $navbar-dropdown-top;
      @include borders($navbar-dropdown-borders);

      .dropdown-header {
        padding: $navbar-dropdown-padding;
        color: $navbar-dropdown-header-color;
        background: $navbar-dropdown-header-background;
      }

      a.dropdown-item {
        font-size: $navbar-dropdown-font-size;
        padding: $navbar-dropdown-padding;

        i, svg {
          margin-right: 10px;
        }
      }
    }
  }

  #nav-quick-search {
    input#quick-search {
      padding: $navbar-search-padding;
      border: $navbar-search-border;
    }

    .advanced-search-button {
      background: None;
      @include borders($navbar-advanced-search-button-border);
    }

    .advanced-search-button-submit {
      @include borders($navbar-advanced-search-button-border);
    }

    .dropdown-menu {
      margin-top: 1px;
      padding: 10px 20px;
      width: 312px;
      @include borders($navbar-dropdown-borders);

      #advanced-search-link {
        padding-top: ($spacer * 0.25);

        a {
          font-size: small;
          text-align: right;
        }
      }
    }
  }
}