$dropdown-font-size: 0.875rem !default;
$dropdown-borders: (
        all: (size: 1px, style: solid, color: $border-color)
);

.dropdown-menu {
  padding: 0;
  margin-top: 7px;
  @include borders($dropdown-borders);

  .dropdown-header {
    padding: 0.3rem 0.8rem;
    color: white;
    background: theme-color('primary');
  }

  a.dropdown-item {
    font-size: $dropdown-font-size;
    padding: 0.3rem 0.8rem;

    i {
      margin-right: 10px;
    }
  }
}