$color_1: white;
$color_2: #A2A2A2;

.error {
	.clip {
		.shadow {
			height: 180px;
			overflow: hidden;
		}
		&:nth-of-type(2) {
			.shadow {
				width: 130px;
				overflow: hidden;
				position: relative;
				box-shadow: inset 20px 0px 20px -15px rgba(150, 150, 150,0.8), 20px 0px 20px -15px rgba(150, 150, 150,0.8);
			}
			.digit {
				left: -10%;
			}
		}
		&:nth-of-type(1) {
			.shadow {
				width: 250px;
				&:after {
					content: "";
					position: absolute;
					right: -8px;
					bottom: 0px;
					z-index: 9999;
					height: 100%;
					width: 10px;
					background: linear-gradient(90deg, transparent, rgba(173,173,173, 0.8), transparent);
					border-radius: 50%;
				}
			}
			.digit {
				right: -20%;
			}
		}
		&:nth-of-type(3) {
			.shadow {
				width: 250px;
				&:after {
					content: "";
					position: absolute;
					right: -8px;
					bottom: 0px;
					z-index: 9999;
					height: 100%;
					width: 10px;
					background: linear-gradient(90deg, transparent, rgba(173,173,173, 0.8), transparent);
					border-radius: 50%;
					left: -8px;
				}
			}
			.digit {
				left: -20%;
			}
		}
	}
	.digit {
		width: 150px;
		height: 150px;
		line-height: 150px;
		font-size: 120px;
		font-weight: bold;
		position: relative;
		top: 8%;
		color: $color_1;
		background: #07B3F9;
		border-radius: 50%;
		display: inline-block;
		transform: skew(45deg);
	}
	h2 {
		font-size: 32px;
		color: $color_2;
		font-weight: bold;
		padding-bottom: 20px;
	}
	.msg {
		top: -190px;
		left: 30%;
		width: 80px;
		height: 80px;
		line-height: 80px;
		font-size: 32px;
		position: relative;
		z-index: 9999;
		display: block;
		background: #535353;
		color: $color_2;
		border-radius: 50%;
		font-style: italic;
	}
	span.triangle {
		top: 70%;
		right: 0%;
		border-left: 20px solid #535353;
		border-top: 15px solid transparent;
		border-bottom: 15px solid transparent;
	}
	.container-error-404 {
		margin-top: 10%;
		position: relative;
		height: 250px;
		padding-top: 40px;
		.clip {
			display: inline-block;
			transform: skew(-45deg);
		}
	}
	.triangle {
		position: absolute;
		z-index: 999;
		transform: rotate(45deg);
		content: "";
		width: 0;
		height: 0;
	}
}
.overlay {
	position: relative;
	z-index: 20;
}
.ground-color {
	background: white;
}
.item-bg-color {
	background: #EAEAEA;
}
.padding-top {
	padding-top: 10px;
}
.padding-bottom {
	padding-bottom: 10px;
}
.padding-vertical {
	padding-top: 10px;
	padding-bottom: 10px;
}
.padding-horizontal {
	padding-left: 10px;
	padding-right: 10px;
}
.padding-all {
	padding: 10px;
}
.no-padding-left {
	padding-left: 0px;
}
.no-padding-right {
	padding-right: 0px;
}
.no-vertical-padding {
	padding-top: 0px;
	padding-bottom: 0px;
}
.no-horizontal-padding {
	padding-left: 0px;
	padding-right: 0px;
}
.no-padding {
	padding: 0px;
}
.margin-top {
	margin-top: 10px;
}
.margin-bottom {
	margin-bottom: 10px;
}
.margin-right {
	margin-right: 10px;
}
.margin-left {
	margin-left: 10px;
}
.margin-horizontal {
	margin-left: 10px;
	margin-right: 10px;
}
.margin-vertical {
	margin-top: 10px;
	margin-bottom: 10px;
}
.margin-all {
	margin: 10px;
}
.no-margin {
	margin: 0px;
}
.no-vertical-margin {
	margin-top: 0px;
	margin-bottom: 0px;
}
.no-horizontal-margin {
	margin-left: 0px;
	margin-right: 0px;
}
.inside-col-shrink {
	margin: 0px 20px;
}
hr {
	margin: 0px;
	padding: 0px;
	border-top: 1px dashed #999;
}
@include media-breakpoint-down('md') {

	.error {
		.clip {
			.shadow {
				height: 100px;
			}
			&:nth-of-type(2) {
				.shadow {
					width: 80px;
				}
			}
			&:nth-of-type(1) {
				.shadow {
					width: 100px;
				}
			}
			&:nth-of-type(3) {
				.shadow {
					width: 100px;
				}
			}
		}
		.digit {
			width: 80px;
			height: 80px;
			line-height: 80px;
			font-size: 52px;
		}
		h2 {
			font-size: 24px;
		}
		.msg {
			top: -110px;
			left: 15%;
			width: 40px;
			height: 40px;
			line-height: 40px;
			font-size: 18px;
		}
		span.triangle {
			top: 70%;
			right: -3%;
			border-left: 10px solid #535353;
			border-top: 8px solid transparent;
			border-bottom: 8px solid transparent;
		}
		.container-error-404 {
			height: 150px;
		}
	}
}
